<template>
  <div>
    <div class="container">
      <el-button
        @click="
          () => {
            this.mobile = '';
            this.showDialog = true;
          }
        "
        type="primary"
        >添加打印员</el-button
      >
    </div>
    <div class="container">
      <el-table :data="list" border style="width: 100%">
        <el-table-column align="center" label="ID" prop="id" width="50" />
        <el-table-column label="打印员">
          <template slot-scope="{ row }">
            <div class="member">
              <div class="member-avatar">
                <img :src="row.avatar" class="member-avatar" alt="" />
              </div>
              <div>
                <div>{{ row.nickname }}</div>
                <div v-if="row.isvip === 2" class="member-tag">会员</div>
              </div>
            </div>
          </template>
        </el-table-column>
        <el-table-column label="手机号" prop="mobile" />
        <el-table-column label="性别">
          <template slot-scope="{ row }">
            <div>{{ { 0: '未知', 1: '男', 2: '女' }[row.gender] }}</div>
          </template>
        </el-table-column>
        <el-table-column label="地区">
          <template slot-scope="{ row }">
            <div>{{ row.province }}/{{ row.city }}</div>
          </template>
        </el-table-column>
        <el-table-column align="center" label="操作" width="150">
          <template slot-scope="{ row }">
            <el-button @click="onDel(row)" size="small" type="text">移除打印员身份</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <div class="container" style="color: #999; font-size: 10px;">
      <i class="el-icon-warning-outline" style="color: #ff4f4f; font-size: 10px;"></i>
      打单软件<a href="http://dldir1.qq.com/WechatWebDev/WechatWayBillHelper/waybillhelper_v1.0.0.zip" style="color: #ff4f4f; margin-left: 3px;" targe="_blank">点此下载</a> (支持 Windows XP 及以上版本), 安装并配置热敏打印机（PC端需安装打印机驱动，并选择203dpi分辨率）
    </div>

    <el-dialog :title="'添加打印员'" :visible.sync="showDialog" width="30%" :before-close="handleClose">
      <el-row>
        <el-col span="19" style="margin-left:10px;">
          <el-input placeholder="手机号, (手机号必须已经成为此小程序的用户)" size="small" v-model="mobile"></el-input>
        </el-col>
      </el-row>
      <span slot="footer" class="dialog-footer">
        <el-button @click="showDialog = false">取 消</el-button>
        <el-button type="primary" @click="onAdd">添加</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
/* eslint-disable */
import Tab from '../../../components/tab/index.vue';
import { M } from '../../../service/grpc.js';
import { rpc, rpc1 } from '../../../service/index.js';
import { getToken } from '../../../service/helper.js';

export default {
  data() {
    return {
      showDialog: false,
      list: [],
      token: '',
      mobile: ''
    };
  },
  async created() {
    this.token = getToken();
    var res = await rpc('StorePrinterService.GetWxPrinterList', M('Token', { token: this.token }));
    this.list = res.userlistList;
  },
  methods: {
    async onAdd() {
      var res = await rpc(
        'StoreUserService.GetUserList',
        M('UserListQueryRequest', {
          token: M('Token', { token: this.token }),
          mobile: this.mobile,
          count: 1
        })
      );
      if (!res.userlistList.length) {
        this.$message({
          message: '未找到该用户',
          type: 'error'
        });
        return;
      }
      var user = res.userlistList[0];
      res = await rpc(
        'StorePrinterService.AddWxPrinter',
        M('IDWithTokenRequest', {
          token: M('Token', { token: this.token }),
          id: user.id
        })
      );
      this.list.splice(0, 0, user);
      this.showDialog = false;
    },
    async onDel(row) {
      var res = await rpc(
        'StorePrinterService.DelWxPrinter',
        M('IDWithTokenRequest', {
          token: M('Token', { token: this.token }),
          id: row.id
        })
      );
      this.list.splice(this.list.findIndex(v => v.id === row.id), 1);
    }
  }
};
</script>

